export default "<template>\n  <div :class=\"$bem({})\">\n    <PageHeader>\n      Icon\n    </PageHeader>\n    <TileExample />\n    <ComponentApi :component=\"component\" />\n    <Story\n      file-name=\"Name\"\n      component-name=\"Icon\"\n      title=\"Name\"\n    >\n      <IconName />\n    </Story>\n    <Story\n      file-name=\"Type\"\n      component-name=\"Icon\"\n      title=\"Type\"\n    >\n      <IconType />\n    </Story>\n    <Story\n      file-name=\"Size\"\n      component-name=\"Icon\"\n      title=\"Size\"\n    >\n      <IconSize />\n    </Story>\n    <Story\n      file-name=\"Rotate\"\n      component-name=\"Icon\"\n      title=\"Rotate\"\n    >\n      <IconRotate />\n    </Story>\n    <Story\n      file-name=\"Flip\"\n      component-name=\"Icon\"\n      title=\"Flip\"\n    >\n      <IconFlip />\n    </Story>\n    <Story\n      file-name=\"Pulse\"\n      component-name=\"Icon\"\n      title=\"Pulse\"\n    >\n      <IconPulse />\n    </Story>\n    <Story\n      file-name=\"Spin\"\n      component-name=\"Icon\"\n      title=\"Spin\"\n    >\n      <IconSpin />\n    </Story>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { defineComponent } from 'vue';\nimport { VIcon } from '@/components';\nimport TileExample from './Example.vue';\nimport {\n  PageHeader,\n  ComponentApi,\n  Story\n} from '@/docs/components';\nimport {\n  IconName,\n  IconType,\n  IconSize,\n  IconRotate,\n  IconFlip,\n  IconPulse,\n  IconSpin\n} from './_stories';\n\nexport default defineComponent({\n  name: 'DocsComponentsIcon',\n  components: {\n    PageHeader,\n    ComponentApi,\n    TileExample,\n    Story,\n    IconName,\n    IconType,\n    IconSize,\n    IconRotate,\n    IconFlip,\n    IconPulse,\n    IconSpin\n  },\n  data () {\n    return {\n      component: VIcon\n    };\n  }\n});\n</script>\n\n<style lang=\"scss\">\n</style>\n";